html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: inherit;
}

#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.controls {
    display: flex;
    width: 100%;
    height: 100%;
    padding-top: 1%;
    justify-content: center;
    align-items: center;
    padding-left: 2.5%;
    padding-right: 2.5%;
}

.control {
    display: flex;
    margin-right: 1%;
    height: 100%;
    width: 20%;
    padding: 0.5%;
    color: #fff;
    justify-content: center;
    align-items: center;
    cursor: default;
    font-weight: bold;
}

.control.disabled {
    color: #000;
}

.control:not(.disabled):hover {
    position: relative;
    z-index: 10;
    transform: scale(1.1);
    cursor: pointer;
}

.control:active {
    transform: scale(1);
}

.control:first-of-type {
    margin-left: 1%;
}

.control.blue {
    background-color: #3c3b6e;
}

.control.red {
    background-color: #b22234;
}

.new-game.blue,
.spymaster-mode.blue {
    background-color: #b22234;
}

.new-game.red,
.spymaster-mode.red {
    background-color: #3c3b6e;
}

.end-turn,
.pause-turn,
.resume-turn {

}

.spymaster-link {
    display: flex;
    height: 100%;
    width: 100%;
    color: inherit;
    justify-content: center;
    align-items: center;
    text-decoration: none;
}


.board {
    display: block;
    width: 100%;
    height: 100%;
    padding: 0.5%;
}

.row {
    display: flex;
}

.cell {
    margin: 0.5%;
    color: white;
    font-weight: bold;
    flex: 1 0 auto;
    position: relative;
}
.cell:after {
    content: "";
    float:left;
    display: block;
    padding-top: 100%;
}

.cell .cell-inner {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card {
    position: relative;
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: transform 0.5s;
}

.card:hover {
    position: relative;
    transform: scale(1.75);
    z-index: 10;
}

.card.flipped:after {
    -webkit-animation: shine 5s ease-in-out  infinite;
            animation: shine 5s ease-in-out  infinite;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    content: '';
    position: absolute;
    top: -100%;
    left: -200%;
    width: 200%;
    height: 200%;
    opacity: 0;
    transform: rotate(30deg);
    background: linear-gradient(
            to right,
            rgba(255, 255, 255, 0.0) 0%,
            rgba(255, 255, 255, 0.0) 80%,
            rgba(255, 255, 255, 0.5) 90%,
            rgba(255, 255, 255, 0.0) 100%
    );
}

.card:not(.flipped):hover {
    cursor: pointer;
}

@-webkit-keyframes shine{
    10% {
        opacity: 1;
        top: -30%;
        left: -30%;
        transition-property: left, top, opacity;
        transition-duration: 0.7s, 0.7s, 0.15s;
        transition-timing-function: ease;
    }
    100% {
        opacity: 0;
        top: -30%;
        left: -30%;
        transition-property: left, top, opacity;
    }
}

@keyframes shine{
    10% {
        opacity: 1;
        top: -30%;
        left: -30%;
        transition-property: left, top, opacity;
        transition-duration: 0.7s, 0.7s, 0.15s;
        transition-timing-function: ease;
    }
    100% {
        opacity: 0;
        top: -30%;
        left: -30%;
        transition-property: left, top, opacity;
    }
}

/* This container is needed to position the front and back side */
.card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.5s;
    transform-style: preserve-3d;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.card.flipped .card-inner {
    transform: rotateY(180deg);
}

/* Position the front and back side */
.card-front, .card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
    background-position: center center;
    background-size: cover;
}

/* Style the back side */
.card-back {
    background-repeat: no-repeat;
    background-size: contain;
    transform: rotateY(180deg);
}

.card-back.blue {
    background-color: #3c3b6e;
}

.card-back.red {
    background-color: #b22234;
}

.card-back.neutral {
    background-color: #fff;
}

.card-back.agent {
    background-image: url(../../static/media/agent.e5a77e2f.svg);
    background-size: auto 80%;
    background-position: center center;
}

.card-back.assassin {
    background-image: url(../../static/media/assassin.b04c41e8.svg);
    background-position: center bottom;
}

.card-back.bystander {
    background-image: url(../../static/media/bystander.52b9f932.svg);
    background-size: auto 85%;
    background-position: center center;
}

.rules {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 1%;
    flex-direction: column;
    background: linear-gradient(
            to right,
            #3c3b6e 0%,
            #b22234 100%
    );
    font-weight: bold;
}

.rules-header {
    text-align: center;
}

.rules-header h1 {
    margin-bottom: 0;
}

.rules-content {
    display: flex;
}

.rule {
    text-align: justify;
    width: 50%;
    padding: 1%;
}

.rule h2 {
    text-align: center;
}

.timer {
    display: block;
    width: 100%;
    height: 100%;
    transition: height 1s;
}

.blinker.blinker {
    -webkit-animation: blinker 1s linear infinite;
            animation: blinker 1s linear infinite;
}

@-webkit-keyframes blinker {
    50% {
        opacity: 0.25;
    }
}

@keyframes blinker {
    50% {
        opacity: 0.25;
    }
}

.timer.blue {
    background-color: #3c3b6e;
}

.timer.red {
    background-color: #b22234;
}

@font-face {
    font-family: Clip;
    src: url("https://acupoftee.github.io/fonts/Clip.ttf");
}

.game {
    display: flex;
    position: relative;
    width: 100vw;
    max-width: 100vh;
    height: 100vw;
    max-height: 100vh;
    flex-wrap: wrap;
    margin: 0 auto;
    font-size: 1.6vw;
    align-items: center;
    justify-content: center;
}

@media (min-aspect-ratio: 1/1) {
    .game {
        font-size: 1.6vh;
    }
}

.game-controls {
    display: flex;
    width: 100%;
    height: 5%;
}

.game-timer {
    display: flex;
    width: 1.5%;
    height: 95%;
    padding-top: 1%;
    padding-bottom: 1%;
}

.game-board {
    position: relative;
    display: flex;
    width: 95%;
    height: 95%;
}

.game-rules {
    display: flex;
    position: absolute;
    top: 6%;
    right: 3.5%;
    left: 3.5%;
    bottom: 1%;
}

.paused:after,
.winner:after {
    content: '';
    position: absolute;
    display: flex;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 1%;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 8em;
}

.paused:after {
    content: 'Paused';
    color: rgba(255, 255, 255, 0.5);
    /*animation: blinker 4s ease-in-out infinite;*/
}

.red.winner:after {
    content: 'Red wins!';
    background: radial-gradient(#b22234, rgba(255, 255, 255, 0.0));
}

.blue.winner:after {
    content: 'Blue wins!';
    background: radial-gradient(#3c3b6e, rgba(255, 255, 255, 0.0));
}

.paused.blue:after {
    background: radial-gradient(#3c3b6e, rgba(255, 255, 255, 0.0));
}

.paused.red:after {
    background: radial-gradient(#b22234, rgba(255, 255, 255, 0.0));
}

@-webkit-keyframes blinker {
    50% {
        opacity: 0;
    }
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.app {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #141114;
  background-image: linear-gradient(335deg, black 23px, transparent 23px),
  linear-gradient(155deg, black 23px, transparent 23px),
  linear-gradient(335deg, black 23px, transparent 23px),
  linear-gradient(155deg, black 23px, transparent 23px);
  background-size: 58px 58px;
  background-position: 0 2px, 4px 35px, 29px 31px, 34px 6px;
  align-items: center;
  justify-content: center;
}

